.confirmation-dialog {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1000;

    &-content {
        background-color: #fff;
        position: fixed;
        top: 50%;
        left: 50%;
        padding: 30px;
        transform: translate(-50%, -50%);
        width: 420px;
    }
}
